
































	export default {
		data(){
			return{
				navLists: [
          {
            name: '推荐',
            status: 0
          },
          {
            name: '信用卡',
            status: 16
          },
          {
            name: '贷款',
            status: 17
          },
          {
            name: '保险',
            status: 18
          }
        ],
        navIndex: 0,
        hotLists: [],
        currentPage: 1, // 当前页
        maxPage: 1, // 最大页
        loading: false // 下拉加载中
			}
		},
    computed: {
      isLoad() {
        return parseInt(this.maxPage) <= parseInt(this.currentPage) && this.hotLists.length > 0
      },
      showNoRecord() {
        return this.hotLists.length <= 0 && !this.loading
      }
    },
		onLoad(){
			this.getLists()
		},
		methods:{
			navChange(n) {
        if (this.navIndex === n) return
        if (this.loading) {
          uni.showToast({
          	title: '正在加载数据',
            icon: 'none'
          })
          return
        }
        this.loading = false
        this.navIndex = n
        this.hotLists = []
        this.currentPage = 1
        this.maxPage = 1
        this.getLists()
      },
      getLists() {
        if (this.loading) return
        this.loading = true
        if (parseInt(this.currentPage) <= 1) {
          this.hotLists = []
        }
        let dataObj = {}
        if (this.navLists[this.navIndex].status === 0) {
          dataObj.hot_recommend = 1
          dataObj.cat_id = ''
        } else {
          dataObj.cat_id = this.navLists[this.navIndex].status
        }
        dataObj.page = this.currentPage
        this.$http({
          url: this.$apis.articleList,
          method: 'GET',
          data: dataObj,
          cb: (err, data) => {
            if (!err && data.success) {
              this.currentPage = data.data.current_page
              this.maxPage = data.data.last_page
              let oldArr = this.hotLists
              let newArr = data.data.data
              this.hotLists = oldArr.concat(newArr)
            } else {
              uni.showToast({
              	title: '获取热文推荐失败',
                icon: 'none'
              })
            }
            this.loading = false
          }
        })
      },
      // 下拉刷新
      scrollUpdate() {
        if (!this.loading && !this.isLoad && this.hotLists.length > 0) {
          this.currentPage ++
          this.getLists()
        }
      },
      gotoContent(id) {
        this.$navigateTo({
        	url: '/pages/spread/content?aId=' + id
        })
      }
		}
	}
